@theme {
    --color-primary: #901726;
    --color-primary-dark: #811623;
    --color-secondary: #EC6229;
    --color-secondary-dark: #DEC87E;
    --color-dark: #292827;
    --font-manrope: "Manrope", sans-serif;

    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --breakpoint-2xl: 1400px;

    --container-sm: 540px;
    --container-md: 720px;
    --container-lg: 960px;
    --container-xl: 1140px;
    --container-2xl: 1320px;
}